<template>
  <div class="contact">
    <h1>Contact info</h1>

    <div class="contact__header">
      <router-link tag="span" to="/" class="btn btn--back" title="back"><span></span></router-link>
      <span v-if="contact" class="btn btn--close" title="remove contact" v-on:click="removeContact"><span></span></span>
    </div>
    <form @submit.prevent="submitHandler" class="contact__body">
      <div class="contact__required">
        <div class="contact__ava">
          <!-- <img class="contact__ava-pic" src="" alt="">-->
        </div>
        <div class="info-block">
          <span class="section-title">Mandatory information</span>
          <div class="row">
            <div class="cell"><label class="fieldName" for="name">Name:</label></div>
            <div class="cell"><input class="fieldValue" v-model="name" type="text" id="name" placeholder="Name" autocomplete="off" required></div>
          </div>
          <div class="row">
            <div class="cell"><label class="fieldName" for="name">Phone:</label></div>
            <div class="cell"><input class="fieldValue" v-model="phone" type="text" id="phone" placeholder="9876543210" autocomplete="off" required></div>
          </div>
        </div>
      </div>
      <div class="contact__additionally">
        <span class="section-title">Additional information</span>
        <div class="row">
          <input class="fieldName" type="text" autocomplete="off" value="fieldName:"/>
          <input class="fieldValue" type="text" placeholder="fieldValue" autocomplete="off">
          <div class="btn btn--close btn--small" title="remove field"><span></span></div>
        </div>
      </div>
      <div class="contact__footer">
        <button type="submit" class="btn btn--apply" title="save"><span></span></button>
      </div>
    </form>

  </div>
</template>

<script>
  export default {
    name: 'Contact',
    data: () => ({
      index: null,
      name: '',
      phone: '',
      extra: null
    }),
    computed: {
      contact() {
        return this.$store.getters.getContactByIndex(+this.$route.params.index)
      }
    },
    mounted() {
      this.name = this.contact ? this.contact.name : '';
      this.phone = this.contact ? this.contact.phone : '';
      this.index = this.$route.params.index;
      this.extra = (this.contact && this.contact.extra) ? this.contact.extra : {}
    },
    methods: {
      submitHandler() {
        const contact = {
          name: this.name,
          phone: this.phone,
        };

        console.log('Передаю индекс - ' + this.index);
        this.$store.dispatch('saveContact', {contact: contact, index: this.index});
        this.$router.push('/')
      },
      removeContact() {
        this.$store.dispatch('removeContact', this.index);
        this.$router.push('/')
      }
    }
  }

</script>

<style scoped>
  .contact {
    padding: 20px;
  }

  .contact__header {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
  }

  .contact__body {
    margin: 20px 0;
  }

  .contact__footer {
    display: flex;
    justify-content: space-around;
    margin-top: 20px;
  }

  .contact__required {
    display: flex;
    flex-flow: row nowrap;
  }

  .contact__ava {
    display: block;
    width: 80px;
    height: 90px;
    border: 2px solid #d1d7de;
    background-color: #ffffff;
    position: relative;
    overflow: hidden;
    font-size: 35px;
  }
  .contact__ava::before,
  .contact__ava::after {
    content: "";
    display: block;
    position: absolute;
    left: 50%;
    border-radius: 50%;
    background-color: #d1d7de;
  }
  .contact__ava::before {
    top: 45%;
    transform: translate(-50%, -50%);
    width: 1em;
    height: 1em;
  }
  .contact__ava::after {
    width: 2em;
    height: 2em;
    bottom: 0;
    transform: translate(-50%, 50%);
  }
  .contact__ava-pic {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    background-color: #fff;
  }

  .info-block {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    padding: 0 0 10px 15px;
  }

  .row {
    display: flex;
    flex-flow: row nowrap;
    justify-content: stretch;
  }

  .row .cell:nth-of-type(2) {
    flex-grow: 1;
  }

  .row .btn {
    min-width: 40px;
  }

  .fieldName,
  .fieldValue {
    display: block;
    width: 100%;
    font-size: 18px;
    line-height: 1.6;
    border: none;
    outline: none;
  }

  .fieldName {
    font-weight: 600;
  }

  .fieldValue {
    flex-grow: 1;
    text-align: right;
  }
</style>